/* Developers */
.dev-container {
  width: 140px;
  height: 140px;
  border-radius: 50%;
  padding: 15px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
}

.social {
  width: 40px;
  height: 40px;
  background: #faebd78a;
  border-radius: 50%;  
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: -1;
  transition: 0.5s;
}

.name {
  color: #212529;
  font-size: 20px;  
  letter-spacing: -10px;
  position: absolute;
  z-index: -1;
  transition: 0.5s;
}

.dev-container img {
  max-width: 100%;
  max-height: 100%;
  border-radius: 50%;
}

.dev-container:hover {
  transform: translateY(25px);
}

.dev-container:hover .name {
  letter-spacing: normal;
  transform: translateY(80px);
}

.dev-container:hover img {
  box-shadow: 0 0 20px rgb(129, 43, 128);
}

.dev-container:hover .social {
  transition-delay: 0.1s;
  box-shadow: 0 0 20px rgb(129, 43, 128);
}

.dev-container:hover .social:nth-child(1) {  
  transform: rotate(-45deg) translateY(-90px);
}

.dev-container:hover .social:nth-child(2) {
  transform:  translateY(-90px);
}

.dev-container:hover .social:nth-child(3) {
  transform:  rotate(45deg) translateY(-90px);
}

.dev-container a {
  transition: 1s;
  text-decoration: none;
  color: #212529;
}

.social:hover a {  
  color: rgb(129, 43, 128);
  transform: rotate(360deg);
}
.main-container{
	display: flex;
	align-items: center;
	min-height: 70vh;
}

.container-login {
	box-shadow: 0 15px 30px 0px #63686d;
	background: #1656a0a6;
    border-radius: 25px;
	display: flex;
	align-items: center;
	justify-content: center;
	max-width: 450px;
	width: 100%;
	position: relative;
}

.dark .container-login {
	box-shadow: 0 15px 30px 0px #20282b;
	color: #fff;
	background: #323435;
}

.con-login {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	border-radius: 40px;
	padding: 35px;
	max-width: 400px;
}

.con-login h1 {
	font-weight: normal;
	font-size: 1.8rem;
}

.con-login .con-inputs {
	padding-bottom: 20px;
	width: 100%;	
}

.con-login .con-inputs .con-new {
	width: 100%;
	text-align: right;
	padding: 5px;
}

.con-input {
	margin-top: 10px;
} 

.con-input input {
	width: 100%;
	padding: 14px 20px;
	border: 0px;
	background: #F1F3F6;
	border-radius: 27px;
	transition: all .25s ease;
}

.con-input input:focus, .btn-login:focus{
	outline: none;
}

.con-input label {
	display: flex;
	padding-left: 8px;
}

.con-login footer {
	width: 100%;
}

.light .con-new a {
	color: #fff;
}

@media (max-width: 500px) {
	.container-login {		
		width: 80%;
		/* border-radius: 0; */
	}
}

.toast {
	z-index: 10000;
	position: fixed;
	top: 80px !important;
    right: 20px !important;
    left: auto !important;	
	padding: 5px 20px;
	height: -webkit-max-content;
	height: -moz-max-content;
	height: max-content;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
	color: white;
}

.error-toast {
	background-color: #c62828;
}

.success-toast {
	background-color: #43a047;	
}
/* Headings */
.mainPageHeading {
  font-size: 72px;
