/* New Cards */
.lec-hover:hover {
  box-shadow: 2px 2px 10px 10px rgba(58, 53, 58, 0.363);
  transform: scale(1.03);
  cursor: default;
}


.lec {
	background-color: #fff;
	border-radius: 10px;
	box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
	display: flex;
	max-width: 100%;
	margin: 20px 20px 20px 0;
	overflow: hidden;
  width: 600px;
  min-height: 280px;
  transition-duration: 0.2s;
}

.dark .lec {
  background-color: #24292b;
  color: #fff;
}

.lec h2, .up h2 {
	letter-spacing: 1px;
	margin: 10px 0;
}

.lec hr {
  background: #8d8c8c;
}

.lec-preview, .ann-preview {
  background-color: #2A265F;
  background: linear-gradient(60deg, #082796c7, #812b80);
	color: #fff;
	padding: 30px;
	width: 50%;
}

.ann-preview {
  width: 40%;
}

.ann-preview img {
  width: 100px;
}

.dark .ann-preview img {
  -webkit-filter: invert(90%);
          filter: invert(90%);
}

.lec-info, .ann-info{
	padding: 20px;
	position: relative;
  width: 100%;
}

.ann-info {
  overflow-wrap: anywhere;
}

.lec .time, .lec .main-data {
  height: 120px;  
}

.lec .time {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.lec-btn, .link-btn {
	border: 0;
	border-radius: 50px;
	box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
	color: #fff;
	font-size: 16px;
	padding: 12px 25px;	
  letter-spacing: 1px;
  text-decoration: none;
}

.link-btn {	
  position: absolute;
  bottom: 10%;
  right: 5%;
}

.lec-btn:hover {
  text-decoration: none;
}

.up:hover {
  box-shadow: 2px 2px 10px 10px rgba(58, 53, 58, 0.363);
  transform: scale(1.03);
  cursor: default;
}


.up {
	background-color: #fff;
	border-radius: 10px;
	box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
