.btn-login {
	margin-top: 20px;
	background: rgb(237, 73, 86);
	color: #fff;
	border: 0px;
	padding: 14px 20px;
    border-radius: 27px;
    position: relative;
	width: 100%;
	display: block;
	cursor: pointer;
	font-size: 1.1rem;
	transition: all .25s ease;	
}

.btn-login:hover {
	transform: translate(0, -5px);
	transition-duration: .2s;
	box-shadow: 0 10px 30px 0 rgba(237, 73, 86, .4);
}

.btn-login:before, button:after {
    position: absolute;
    opacity: 0;
    border-radius: 50%;
    background-color: #fff;
    top: 50%;
    left: 50%;
    margin-top: -1.125rem;
    margin-left: -1.125rem;
    width: 2.25rem;
    height: 2.25rem;
    content: '';
    z-index: 1;
}
.btn-login.loading {
    display: inline-block;
    border-radius: 50%;
    height: 2.75rem;
    -webkit-animation: button-loading 0.5s forwards;
            animation: button-loading 0.5s forwards;
}
.btn-login.loading:hover {
    transform: none;
    box-shadow: none;
}
.btn-login.loading:before {
    opacity: 1;
    -webkit-animation: button-dot-intro 0.5s forwards;
            animation: button-dot-intro 0.5s forwards;
}
.btn-login.loading:after {
    opacity: 0;
    -webkit-animation: button-dot-pulse 1.5s infinite 0.5s;
            animation: button-dot-pulse 1.5s infinite 0.5s;
}
@-webkit-keyframes button-loading {
    20% {
        color: transparent;
        transform: scale(1, 1);
   }
    40% {
        border-color: rgb(237, 73, 86);
        background-color: transparent;
        transform: scale(1, 1);
   }
    60% {
        transform: scale(0.7, 1.1);
        margin-left: 1.25rem;
        width: 2.5rem;
        text-indent: -0.6125rem;
        color: transparent;
        border-color: rgb(237, 73, 86);
   }
    80% {
        transform: scale(1, 1);
   }
    100% {
        margin-left: 1.25rem;
        width: 2.5rem;
        background-color: rgb(237, 73, 86);
        color: transparent;
   }
}
@keyframes button-loading {
    20% {
        color: transparent;
        transform: scale(1, 1);
   }
    40% {
        border-color: rgb(237, 73, 86);
        background-color: transparent;
        transform: scale(1, 1);
   }
    60% {
        transform: scale(0.7, 1.1);
        margin-left: 1.25rem;
        width: 2.5rem;
        text-indent: -0.6125rem;
        color: transparent;
        border-color: rgb(237, 73, 86);
   }
    80% {
        transform: scale(1, 1);
   }
    100% {
        margin-left: 1.25rem;
        width: 2.5rem;
        background-color: rgb(237, 73, 86);
        color: transparent;
   }
}
@-webkit-keyframes button-dot-intro {
    0% {
        opacity: 0;
   }
    60% {
        opacity: 1;
        transform: scale(1, 1);
   }
    100% {
        transform: scale(0.75, 0.75);
   }
}
@keyframes button-dot-intro {
    0% {
        opacity: 0;
   }
    60% {
        opacity: 1;
        transform: scale(1, 1);
   }
    100% {
        transform: scale(0.75, 0.75);
   }
}
@-webkit-keyframes button-dot-pulse {
    0% {
        opacity: 1;
        transform: scale(0.75, 0.75);
   }
    15% {
        transform: scale(0.85, 0.85);
   }
    45% {
        transform: scale(0.75, 0.75);
   }
    55% {
        transform: scale(0.95, 0.95);
   }
    85% {
        transform: scale(0.75, 0.75);
   }
    100% {
        opacity: 1;
        transform: scale(0.75, 0.75);
   }
}
@keyframes button-dot-pulse {
    0% {
        opacity: 1;
        transform: scale(0.75, 0.75);
   }
    15% {
        transform: scale(0.85, 0.85);
   }
    45% {
        transform: scale(0.75, 0.75);
   }
    55% {
        transform: scale(0.95, 0.95);
   }
    85% {
        transform: scale(0.75, 0.75);
   }
    100% {
        opacity: 1;
        transform: scale(0.75, 0.75);
   }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

nav {
  background-color: #d4cece;
  height: 8vh;
  font-size: 2.3vh;
  display: flex;
}

.dark nav {
  background-color: #24292b;
}

.brand {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 15%;
}

.brand li {
  color: #000000;
  font-size: 4vh;
  list-style: none;
  transition: all 0.3s ease;
  opacity: 0.8;
  position: absolute;
  left: 70px;
  z-index: 2;
}

.dark .brand li {
  color: #ffffff;
}

.brand li:hover {
  font-size: 4.5vh;
  opacity: 1;
}

.logo {
  height: 6vh;
  width: 6vh;
  position: absolute;
  top: 1vh;
  left: 1vh;
  border-radius: 15% 15%;
  z-index: 2;
}

.nav-links {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 60%;
  height: 100%;
  margin-left: auto;
  margin-right: 5vw;
  list-style: none;
  z-index: 1;
}

.nav-link {
  color: #000000;
  text-transform: uppercase;
  letter-spacing: 2px;
  opacity: 0.7;
  transition: 0.3s ease;
}

.dark .nav-link {
  color: #ffffff;
}

.nav-link:hover {
  text-decoration: none !important;
  font-size: 2.7vh;
  color: white;
  opacity: 1;
}

@media screen and (max-width: 900px) {
  nav {
    position: relative;
  }

  .nav-links {
    position: absolute;
    background-color: #d4cece;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    clip-path: circle(100px at 100% -15%);
    -webkit-clip-path: circle(100px at 100% -15%);
    transition: all 1s ease-in-out;
    pointer-events: none;
  }

  .dark .nav-links {
    background-color: #24292b;
  }

  .nav-links.open {
    clip-path: circle(1300px at 100% -15%);
    -webkit-clip-path: circle(1300px at 100% -15%);
    pointer-events: all;
  }
  .nav-link:hover {
    font-size: 3.5vh;
  }
  .hamburger {
    border: 1px solid #000000;
    border-radius: 15% 15%;
    cursor: pointer;
    position: absolute;
    right: 10%;
    top: 50%;
    transform: translate(50%, -50%);
    z-index: 2;
  }
  .dark .hamburger {
    border: 1px solid #ffffff;
  }
  .dark .line {
    background-color: #ffffff;
  }
  .line {
    height: 3px;
    width: 30px;
    margin: 5px;
    background-color: #000000;
  }
}
.loader {
  height: 40vh;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: center;
  vertical-align: middle;
  margin-top: 50vh;
  position: relative;
  display: flex;
}

.loader span {
  display: block;
  width: 20px;
  height: 20px;
  background: #fe8e07;
  border-radius: 50%;
  margin: 0 5px;
  box-shadow: 3px 4px 4px rgba(0, 0, 0, 0.2);
}

.loader span:nth-child(2) {
  background: #ff7020;
}

.loader span:nth-child(3) {
  background: #ff4d41;
}

.loader span:nth-child(4) {
  background: #ff2466;
}

.loader span:nth-child(5) {
  background: #ff087d;
}


@-webkit-keyframes animate {
  0% {
    background: #fe8e07;
    transform: translateX(0);
  }
  12.25% {
    background: #ff7020;
  }
  24.5% {
    background: #ff4d41;
  }
  36.75% {
    background: #ff2466;
  }
  
  49% {
    background: #ff087d;
    transform: translateX(120px);
  }
  51% {
    background: #ff087d;
    transform: translateX(120px);
  }
  63.25% {
    background: #ff2466;
  }
  75.5% {
    background: #ff4d41;
  }
  87.75% {
    background: #ff7020;
  }
  100% {
    background: #fe8e07;
    transform: translateX(0px);
  }
}


@keyframes animate {
  0% {
    background: #fe8e07;
    transform: translateX(0);
  }
  12.25% {
    background: #ff7020;
  }
  24.5% {
    background: #ff4d41;
  }
  36.75% {
    background: #ff2466;
  }
  
  49% {
    background: #ff087d;
    transform: translateX(120px);
  }
  51% {
    background: #ff087d;
    transform: translateX(120px);
  }
  63.25% {
    background: #ff2466;
  }
  75.5% {
    background: #ff4d41;
  }
  87.75% {
    background: #ff7020;
  }
  100% {
    background: #fe8e07;
    transform: translateX(0px);
  }
